
.recipesheet{position:relative;width:600px;margin:0 20px;background-color:#fff
}
.editer-section{width:100%;margin-bottom:0px;padding:20px;display:-ms-flexbox;display:flex
}
.editer-section-title{font-size:10pt;font-weight:bold;color:#616161;margin-bottom:0.5em;width:90px;padding-right:40px;text-align:right
}
.editer-section-body{width:400px;padding-right:20px;-ms-flex-positive:1;flex-grow:1
}
.foods .food{transition:all .25s ease;border-top:1px solid #eee
}
.foods .food:last-child{border-bottom:1px solid #eee
}
.foods .food td:first-child{width:1.5em;position:relative;top:0.6em
}
.foods .food td:last-child{width:80px
}
.foods .food td.info{width:140px
}
.foods .unit{width:5em
}
.foods .food-title{transition:all .25s ease;font-size:12pt;cursor:pointer
}
.foods .diagram{text-align:center
}
.input-item{transition:all .25s ease;opacity:1;font-size:8pt;color:#616161;margin-bottom:1px
}
.input-item .label{width:4em;text-align:right
}
input[type="number"]{font-size:9pt;height:2.2em;width:2em
}
.food.disabled .food-title{font-size:8pt;color:#E0E0E0
}
.food.disabled .input-item{opacity:0;font-size:4pt
}
.food.disabled input[type="number"]{height:0em
}
.step{border-top:1px solid #eee
}
.step td:first-child{width:80px
}
.step td:last-child{width:120px
}
.step .source{font-size:8pt;color:#757575
}
.step .icon{position:relative;width:80px;height:80px;cursor:pointer
}
.step .icon input{position:relative;z-index:9999
}
.step .icon .img-container{position:absolute;top:0;left:0;width:80px;height:80px;padding:15px
}
.step .icon .img-container img{width:100%;height:100%
}
.step.disabled img{transition:all .25s ease;opacity:0.2
}
.step.disabled .step-title{color:#E0E0E0
}
.step.disabled .source{color:#eee
}
.step.disabled .input-item{opacity:0;font-size:4pt
}
.step.disabled input[type="number"]{height:0em
}
.results{display:-ms-flexbox;display:flex
}
.results .time-and-price,.results .diagram{width:50%
}
.results .time-and-price{vertical-align:top
}
.results .diagram{display:-ms-flexbox;display:flex;-ms-flex-align:top;align-items:top
}
.results .result{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.results .result .label{color:#424242;font-size:10pt
}
.results .result .unit{color:#424242;width:8em
}
.results .diagram .result{-ms-flex-align:start;align-items:flex-start
}
.result-section{border-top:1px solid #BDBDBD
}
.header-section{-ms-flex-align:end;align-items:flex-end;padding-top:30px;padding-bottom:10px
}
.header-section .editer-section-body{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between
}
.header-section .editer-section-body .input-item{width:120px
}
.header-section .recipe-title{font-size:16pt
}
.remove-btn{position:absolute;top:5px;right:10px;padding:5px;color:#E0E0E0;transition:all .25s ease;cursor:pointer
}
.remove-btn:hover{color:#F44336
}



