
.app-header{position:fixed;top:0;left:0;width:100%;height:50px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;background-color:#fff;border-bottom:1.5px solid #D2C8BE;z-index:8000
}
.app-header{background-color:#fff
}
.app-header nav{height:100%
}
.app-header nav ul{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;margin-left:1em;height:100%
}
.app-header nav ul li{height:100%
}
.app-header a{display:block;width:100%;height:100%;text-decoration:none;font-size:10pt;padding:0 1.5em;color:#BDBDBD;line-height:50px
}
.app-header a:hover{background-color:#eee
}
.app-header a.active{color:#00838F;background-color:#FFF9C4
}
.app-header h1{padding:0 1.5em;font-size:11pt;color:#000;font-weight:bold
}
.app-header h1.active{color:#000
}
.app-header h1:hover{background-color:transparent;color:#616161
}



