
.page{display:-ms-flexbox;display:flex
}
.page .page-info{padding:0 40px;width:300px;height:100%;background-color:#fff;border-right:1px solid #E0E0E0
}
.page .page-body{width:100px;-ms-flex-positive:1;flex-grow:1
}
.meta{padding-top:40px
}
.meta input[type="text"]{width:240px
}
.meta .label{font-size:11pt;color:#424242
}
.lay-scroll-y-parent{width:100%;overflow:hidden;padding:20px 40px;overflow-x:auto;white-space:nowrap
}
.lay-scroll-y-child{display:inline-block;vertical-align:top;white-space:normal
}
.newRecipeSheet .inner{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center
}
.newRecipeSheet .inner .label{color:#757575;font-size:9pt;margin-top:1em;cursor:pointer;transition:all .25s ease
}
.newRecipeSheet .inner .label:hover{color:#42A5F5
}



