
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{margin:0;padding:0
}
table{border-collapse:collapse;border-spacing:0
}
fieldset,img{border:0
}
address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:normal
}
ol,ul{list-style:none
}
caption,th{text-align:left
}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal
}
q:before,q:after{content:''
}
abbr,acronym{border:0;font-variant:normal
}
sup{vertical-align:text-top
}
sub{vertical-align:text-bottom
}
input,textarea,select{font-family:inherit;font-size:inherit;font-weight:inherit;*font-size:100%
}
legend{color:#000
}
html,body{background-color:#fff
}
body,h1,h2,h3,h4,h5,h6,p,a,span,li,dl,dt,dd,input{font-family:'Lato', 'Hiragino Kaku Gothic Pro', Helvetica, Arial, sans-serif
}
*{webkit-box-sizing:border-box;-o-box-sizing:border-box;-ms-box-sizing:border-box;box-sizing:border-box
}
.lay-twoColumn{display:-ms-flexbox;display:flex;width:100%
}
.lay-twoColumn .lay-column{width:100%;-ms-flex-negative:1;flex-shrink:1
}
.lay-twoColumn .lay-column:first-child{margin-right:10px
}
.lay-twoColumn .lay-column:last-child{margin-left:10px
}
.operator{width:1em;padding-bottom:0.3em;font-size:14pt;color:#757575
}
.unit{padding-left:0.3em;font-size:8pt;color:#757575;width:3em
}
.input-item{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between
}
.input-item:not(:last-child){margin-bottom:1px
}
.input-item .input-item-title{font-size:10pt;color:#607D8B
}
.input-item .input-item-title{width:6em;padding-right:10px
}
.input-item .input-item-value{-ms-flex-positive:1;flex-grow:1;display:-ms-flexbox;display:flex;-ms-flex-align:end;align-items:flex-end
}
.input-item.input-item-pick-title{-ms-flex-direction:column;flex-direction:column;-ms-flex-align:start;align-items:flex-start;padding-bottom:1em
}
.input-item.input-item-pick-title .input-item-title,.input-item.input-item-pick-title .input-item-value{width:100%
}
table{width:100%
}
input[type="number"],input[type="text"]{-ms-flex-positive:1;flex-grow:1;width:50px;height:2.5em;background-color:transparent;font-size:12pt;outline:none
}
input[type="number"]{height:2.5em;border-radius:5px;border:1px solid #E0E0E0;text-align:center
}
input[type="number"]:hover{border:1px solid #BDBDBD
}
input[type="number"]:not(:disabled):focus{border:1px solid #00ACC1
}
input[type="text"]{border:none;border-bottom:1px solid #E0E0E0
}
input[type="text"]:hover{border-bottom:1px solid #BDBDBD
}
input[type="text"]:not(:disabled):focus{border-bottom:1px solid #00ACC1
}
html,body,#app{position:relative;width:100%;height:100%;background-color:#F7F3E8
}
.app-body{position:relative;width:100%;height:100%;padding-top:25px;overflow-x:hidden
}



